<template>
	<div>
		<form @submit.prevent="createEvent">
			<p class="title">–î–æ–±–∞–≤–∏—Ç—å —Å–æ–±—ã—Ç–∏–µ</p>
			<select v-model="isIncome">
				<option :value="true">
					–î–æ—Ö–æ–¥
				</option>				
				<option :value="false">
					–†–∞—Å—Ö–æ–¥
				</option>				
				<option :value="'debt'">
					–î–æ–ª–≥
				</option>				
				<option :value="'debtRefund'">
					–í–æ–∑–≤—Ä–∞—Ç –¥–æ–ª–≥–∞
				</option>
			</select>
			<input type="text" v-model="name" placeholder="–ö—Ç–æ –º–Ω–µ –¥–æ–ª–∂–µ–Ω / –ö–æ–º—É —è –¥–æ–ª–∂–µ–Ω" v-if="isIncome === 'debt'">
			<input type="text" v-model="name" placeholder="–ö—Ç–æ –º–Ω–µ –≤–µ—Ä–Ω—É–ª / –ö–æ–º—É —è –≤–µ—Ä–Ω—É–ª" v-if="isIncome === 'debtRefund'">
			<input type="text" v-model="text" placeholder="–û–ø–∏—Å–∞–Ω–∏–µ">
			<input type="text" v-model.number="value" placeholder="–°—É–º–º–∞">
			<select v-model="type" v-if="!isIncome || isIncome === 'debt' || isIncome === 'debtRefund'">
				<option :value="null" disabled>–í—ã–±–µ—Ä–∏ –∫–∞—Ç–µ–≥–æ—Ä–∏—é</option>

				<template v-if="isIncome != 'debt' && isIncome != 'debtRefund'">
					<option v-for="(type,i) in categories" :value="type.translite" :selected="!i">
						{{type.name + type.icon}}
					</option>
				</template>

				<template v-else>
					<option :value="true">
						–ú–Ω–µ –¥–æ–ª–∂–Ω—ã / –ú–Ω–µ –≤–µ—Ä–Ω—É–ª–∏
					</option>
					<option :value="false">
						–Ø –¥–æ–ª–∂–µ–Ω / –Ø –≤–µ—Ä–Ω—É–ª
					</option>
				</template>

			</select>
			<button type="submit" class="crm-btn">
				–î–æ–±–∞–≤–∏—Ç—å
			</button>				
		</form>		
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import moment from 'moment';
import { transliterate as tr, slugify } from 'transliteration';
export default{
	name:'createEvent',
	props:['month'],
	computed:{
	    ...mapGetters([
	      'categories',
	      'randomMoments',
	    ])
	},
	data(){
		return{
			name:null,
			value:null,
			date:moment(new Date()).format('DD.MM.YYYY'),
			type:null,
			isIncome:true,
			text: null
		}
	},
	methods:{
		randomPlaceholder(){
			const rand = Math.floor(Math.random() * Math.floor(this.randomMoments.length));
			return this.randomMoments[rand]
		},
		createEvent(){
			console.log('Create start')
			if(!this.isIncome && !this.type) {
				this.notifyForRequiredFields()
				return false					
			}
			if(!this.text || !this.value){
				this.notifyForRequiredFields()
				return false
			}
			if(isNaN(this.value / 1)){
				this.notifyForRequiredFields('–ó–Ω–∞—á–µ–Ω–∏–µ–º –º–æ–∂–µ—Ç –±—ã—Ç—å —Ç–æ–ª—å–∫–æ —á–∏—Å–ª–æ')
				return false
			}
			if(this.isIncome) {
				if(this.isIncome === 'debt'){
					const event = {
						me: this.type,
						name: this.name,
						description: this.text,
						value: this.value,
						date: moment(new Date()).format('DD.MM.YYYY'),
						complited: false
					}
					console.log(event)
					this.$store.dispatch('addDebts',event)
					.then(()=> {
						this.name = null
						this.value = null
						this.text = null
						this.date = moment(new Date()).format('DD.MM.YYYY')
						this.isIncome = true
					})
				}
				if(this.isIncome === "debtRefund"){
					const event = {
						me: this.type,
						name: this.name,
						description: this.text,
						value: this.value,
						date: moment(new Date()).format('DD.MM.YYYY'),
						complited: false
					}
					console.log(event)
					this.$store.dispatch('addDebtsIcnome',event)
					.then(()=> {
						this.name = null
						this.value = null
						this.text = null
						this.date = moment(new Date()).format('DD.MM.YYYY')
						this.isIncome = true
					})
				} else {	
					const event = {
						body: {
							name: this.text,
							value: +this.value,
							date: this.date,
							type: null,
							uid: null,
							isIncome: this.isIncome ? true : false
						},
						month: moment(new Date()).format('MM-YYYY')
					}
					this.$store.dispatch('createEvent',event)
					.then(()=> {
						this.name = null
						this.value = null
						this.date = moment(new Date()).format('DD.MM.YYYY')
						this.isIncome = true
					})
				}
			} 
			else{
				const event = {
					body: {
						name: this.text,
						value: +this.value,
						date: this.date,
						type: this.type,
						uid: null,
						isIncome: this.isIncome ? true : false						
					},
					month: moment(new Date()).format('MM-YYYY')
				}
				this.$store.dispatch('createEvent',event)
				.then(()=> {
					this.name = null
					this.value = null
					this.date = moment(new Date()).format('DD.MM.YYYY')
					this.isIncome = true
				})
			}
		},
		notifyForRequiredFields(err){
			this.$notify({
			  group: 'foo',
			  title: '–û—à–∏–±–∫–∞',
			  type:'error',
			  text: err || '–ó–∞–ø–æ–ª–Ω–∏ –≤—Å–µ –ø–æ–ª—è'
			});
		}
	},
	mounted(){

	},
	watch:{
		isIncome: function (argument) {
			console.log('isIncome:',this.isIncome)
		}
	}
}	
</script>

