import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import Notifications from 'vue-notification'
import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

Vue.config.productionTip = false

const globalMethods = {
  methods: {
	formattingTypeName(name,typesList){
		if(!typesList) return name
		const resultName = typesList.find(t => t.translite === name) || undefined
		return resultName ? `${resultName.name} ${resultName.icon}` : name
	},
	moneyFormatting(money){
		const subfix = '‚ÇΩ'
		const separator = '.'
		if(money.length < 4) return money + subfix
		else {
			// –ü–µ—Ä–µ–≤–æ–¥–∏–º —á–∏—Å–ª–æ –≤ —Å—Ç—Ä–æ–∫—É –∏ –ø—Ä–æ–±–µ–≥–∞–µ–º—Å—è —Ü–∏–∫–ª–æ–º, 
			// –¥–æ–±–∞–≤–ª—è—è –∫–∞–∂—ã–¥–π —Å–∏–º–≤–æ–ª –≤ –Ω–æ–≤—ã–π –º–∞—Å—Å–∏–≤
			const str = money.toString()
			let arr = []
			for(let i = 0;i<str.length;i++){
				arr.push(str[i])
			}
			// –†–∞–∑–≤–æ—Ä–∞—á–∏–≤–∞–µ–º –µ–≥–æ, —Ç.–∫. —Å—á–∏—Ç–∞—Ç—å –æ—Ç—Å—Ç—É–ø—ã –±—É–¥–µ–º —Å–ø—Ä–∞–≤–∞
			arr = arr.reverse()
			// –ü–æ—Å–ª–µ —ç—Ç–æ–≥–æ, –∑–∞–¥–∞—ë–º –ø–æ—Å–ª–µ–¥–æ–≤–∞—Ç–µ–ª—å–Ω–æ—Å—Ç—å
			// —á–µ—Ä–µ–∑ –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ –±—É–¥–µ—Ç –≤—ã—Å—Ç–∞–≤–ª—è—Ç—å —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å
			let step = 3
			// –ú–∞–ø–ø–∏–º –º–∞—Å—Å–∏–≤ –≤ —Å—Ç–∞–≤–ª—è–µ–º –Ω–∞ –∫–∞–∂–¥–æ–º 3–º —à–∞–≥–µ –Ω–∞—à —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å
			return arr.map((el,i)=>{
				if(i == step){
					step = step * 2
					return el + separator
				} else {
					return el
				}
			})
			//–ü–µ—Ä–µ–≤–æ—Ä–∞—á–∏–≤–∞–µ–º –æ–±—Ä–∞—Ç–Ω–æ, –æ–±—ä–µ–¥–µ–Ω—è–µ–º –≤ —Å—Ç—Ä–æ–∫—É –∏ –¥–æ–±–∞–≤–ª—è–µ–º –ø–æ—Å—Ç—Ñ–∏–∫—Å
			.reverse()
			.join('') + subfix			
		}
	},
	displayList(list,search){
		if(!search) return list
		else {
			let re = new RegExp(search, 'i') 
			return list.filter((el) => {
				return re.test(JSON.stringify(el)) ? true : false
			})
		}		
	}
  }
}

Vue.use(Notifications)
Vue.mixin(globalMethods)

firebase.initializeApp({
	apiKey: "AIzaSyB8u1jKgcxlVeRva1QmUYHhZ0m_8-r7Hsg",
	authDomain: "my-cool-crm.firebaseapp.com",
	databaseURL: "https://my-cool-crm.firebaseio.com",
	projectId: "my-cool-crm",
	storageBucket: "my-cool-crm.appspot.com",
	messagingSenderId: "1129947099",
	appId: "1:1129947099:web:fae016581a7349b41311fe"
})

let app

firebase.auth().onAuthStateChanged(()=>{
	if(!app){
		app = new Vue({
		  router,
		  store,
		  render: h => h(App)
		}).$mount('#app')		
	}
})


