                            <input type="password" v-model="password">
                            <button type="submit" class="crm-btn">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</button>
                        </form>
                        <p>                       
                            <router-link to="/login">
                                –ù–∞–∑–∞–¥ 
                            </router-link>
                        </p>                             
                    </div>    
                </div>
