<script>
export default{
    name:'register',
    data(){
        return {
            email: '',
            password: '',
            name: '',
            startCapital: '',
        }
    },
    methods:{
        async auth(){
            try {
                await this.$store.dispatch('register',{email:this.email,password:this.password,name:this.name})
